@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Roboto+Mono:wght@300;400;500;600;700&display=swap");
.no-gutters {
  margin-right: 0;
}

.logo a {
  background: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week5/logo-ANSWER.svg) no-repeat;
  height: 40px;
  display: block;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
}

.nav {
  padding: 40px 0 32px 32px;
  min-width: 296px;
  min-height: 100vh;
}

.nav-link {
  padding: 0;
}

.nav-item .active ::before {
  content: "";
  position: absolute;
  left: 0;
  margin-top: -2px;
  width: 4px;
  height: 30px;
  border-left: 4px solid #198754;
}

.menu-user a {
  font-size: 14px;
}

.menu-user img {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.main .fs-7 {
  font-size: 12px;
  line-height: 15px;
}

.main .header {
  padding: 32px 32px 18px 32px;
}

.main .list-group {
  border-radius: 0;
}

.main .list-group .active {
  background-color: #F8F9FA;
  border: 1px solid #CED4DA;
  color: #212529;
}

.main .card {
  border-radius: 0;
}

.main .card-body img {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.main .card-body .btn {
  width: 35%;
  border: 1px solid #CED4DA;
  border-radius: 2px;
  padding: 10px 4px 10px 3px;
}

.main .card-body .card-text {
  font-size: 14px;
  line-height: 24px;
}

.main .card-body .card-text span {
  color: #198754;
  background-color: #F8F9FA;
}

.main .assign-footer .btn {
  border-radius: 2px;
  padding: 10px 20px;
}

.main .assign-footer ul {
  margin: 0;
}

.main .assign-footer .page-icon li a:hover {
  color: #198754;
}

body {
  font-family: 'Lato','Roboto', sans-serif;
}

img {
  display: block;
  /*vertical-align: middle;  去除圖片底下的2-3px空白 */
  max-width: 100%;
  /* 使之貼其父元素，才不會有圖片過大情況發生 */
  height: auto;
  object-fit: cover;
}

a {
  display: block;
  text-decoration: none;
}

p {
  margin: 0;
}
